@use "../abstracts" as *;

.destinationInfo {
  margin: 8rem 0;
  @include responsive(xsmall) {
     margin: 4rem 0;
  }
  &__p {
    font-size: 1.6rem;
    color: $silverDark;
    line-height: 2.8rem;
    @include responsive(medium) {
      margin-bottom: 2rem;
    }
  }

  &__details {
  //align($flex, $justify-content, $align-items, $height)
    @include  align(flex, space-between, center, null);
    padding: 1.3rem 0;
    border-bottom: 1px dotted $silverLight-50;
    &__head {
      font-size: 1.6rem;
      color: $silverDark;
    }
    &__text {
      font-size: 1.6rem;
      color: $silverDark;
    }
  }
}