@use "../abstracts" as *;

.footer {
  width:  100%;
  min-height: 60rem;
  margin-top: 8rem;
  padding: 8rem 0;
  @include responsive(xsmall) {
    margin-top: 4rem;
    padding: 4rem 0;
  }
  //background: url("/assets/footer/footer.jpg");
  &__container {
    width: 100%;
    &__block {
      background: $white;
      padding: 2rem;
    }
  }
  &__intro {
    &__img {
      width: 15rem;
      @include responsive(small) {
         width: 13rem;
      }
      @include responsive(xsmall) {
        width: 10rem;
      }
      img {
        width: 100%;
        height: auto;
        object-fit: cover;
      }
    }

    &__msg {
      margin-top: 2rem;
      font-size: 1.6rem;
      color: $silverlight;
      width: 80%;
      font-weight:  400;
      line-height: 2.5rem;
      @include responsive(xsmall) {
        width: 100%;
        font-size: 1.4rem;
      }
    }
  }
  &__heading {
    color: $black;
    text-transform: uppercase;
    font-size: 1.6rem;
    letter-spacing: .2rem;
    margin-bottom: 2rem;
    @include responsive(xsmall) {
        font-size: 1.4rem;
    }
  }
  &__ul {
    list-style: none;
    &__li {
      @include align(flex, null, center, null);
      color: $silverlight;
      a {
        text-decoration: none;
        color: $silverlight;
        display: block;
        padding: .5rem 0;
        font-size: 1.5rem;
        margin-left: .5rem;
        &:hover {
          color: deeppink;
        }
      }
    }
  }

  &__bottom {
     width: 100%;
     background: linear-gradient(to right, $main1, $main2);
     color: $white;
     height: 5rem;
  //align($flex, $justify-content, $align-items, $height)
     @include align(flex, center, center, null);
     p {
       font-size: 1.4rem;
       text-transform: capitalize;
       letter-spacing: .08rem;
       @include responsive(xsmall) {
          font-size: 1.4rem;
       }
     }
  }
}