@use "../abstracts" as *;

.page {
   margin: 8rem 0;
  &__image {
    width:100%;
    height: auto;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
   &__info {
     margin-left: 4rem;
     &__heading {
        font-size: 4rem;
        font-weight: 900;
        line-height: 5rem;
        color: $black;
       text-transform: capitalize;
     }
     &__msg {
       margin-top: 2rem;
       font-size: 1.5rem;
       line-height: 2.6rem;
       color: $silverlight;
     }
     &__link {
       margin-top: 2rem;
     }
   }
  & .container .row {
    //background: pink;
    align-items: center;
  }
  &__contact__heading {
    font-size: 2rem;
    margin-bottom: 2rem;
    font-weight: 400;
    text-transform: capitalize;
  }
}